.nav {
    background-color: #87859f;
    padding: 20px;
}

.item a {
    color: white;
    text-decoration: none;
}

.item a.activeLink {
    color: gold;
}

* {
    margin: 0;
    box-sizing: border-box;
}

.burger {
    display: block;
    position: absolute;
    right: 0;
    top: 0;
    overflow: hidden;
    margin: 0;
    padding: 0;
    width: 96px;
    height: 96px;
    font-size: 0;
    border: none;
    box-shadow: none;
    cursor: pointer;
}
.burger:focus {
    outline: none;
}

.burger span {
    display: block;
    position: absolute;
    top: 44px;
    left: 18px;
    right: 18px;
    height: 8px;
    background: #fff;
}
.burger span::before, .burger span::after {
    position: absolute;
    display: block;
    left: 0;
    width: 100%;
    height: 8px;
    background: #fff;
    content: '';
}

.burger span::before {
    top: -20px;
}

.burger span::after {
    bottom: -20px;
}

.burger-line {
    background: #a10017;
}

.burger-line span {
    transition: background 0s 0.3s;
}






